export default {"name":"江华毛尖","img":"\\lvcha\\jianghuamaojian\\img_20191122100136tyuf12.jpg","desc":["具有数百年历史的江华毛尖，生产区集中顺牛牯岭、岭东的大圩开源冲、两岔河一带的云雾山腰和溪谷之傍，那里苍峰入云，林荫如盖，云雾潦绕，真可谓“高山云雾出好茶”，特殊的地理环境造就了名茶——江华毛尖。"],"menus":[{"name":"江华毛尖茶叶特点","content":[{"text":"江华毛尖\n        品质特征：外形条索肥厚，紧结卷曲，白毫显露，内质香气清高，汤色晶莹，滋味浓醇甘爽，叶底嫩绿。内含茶多酚、氨基酸、氮丰富。饮用“止渴生津”;药用可治“积热、久泻”和“心脾不舒”，去烦闷。江华县山区瑶胞用以作解毒去病常备药品，据说在古代时被纳为贡品。"},{"src":"\\lvcha\\jianghuamaojian\\img_20191122100136jl6t9v.jpg"}]},{"name":"江华毛尖加工工艺","content":[{"text":"江华毛尖\n        的加工工艺分为杀青、摊凉、揉捻、复炒、摊凉、复揉、整形和足干八道工序。"},{"text":"杀青在平锅进行，杀青出锅后，须经摊凉，以散热气，防止闷黄。摊凉后的杀青叶置于揉板上重揉5～6分钟，待茶汁揉出后，置于100℃左右的锅内进行复炒，炒到茶叶五成干时，出锅摊凉，稍经复揉后，投入温度为60～80℃的锅内进行整形，这是决定江华毛尖品质特色的重要工序。"},{"text":"茶叶投入锅中后，略加翻炒，待茶叶受热回软时，用双手边翻拌，边搓条，如此反复进行约30分钟，待茶叶含水量达15%左右，此时茶条已紧细、翠润、显毫。最后将茶叶移入锅温为50～60℃的锅中，轻轻翻炒，直至足干。"}]},{"name":"江华毛尖储藏方法","content":[{"text":"1、铁罐的储藏法"},{"text":"选用市场上供应的马口铁双盖彩色茶向做盛器。储存前，检查罐身与罐盖是否密闭，不能漏气。储存时，将干燥的茶叶装罐，罐要装实装严。这种方法采用方便，但不宜长期储存。"},{"text":"2、热水瓶的储藏法"},{"text":"选用保暖性良好的热水瓶作盛具。将干燥的茶叶装入瓶内，装实装足，尽量减少空气存留量，瓶口用软木塞盖紧，塞缘涂白蜡封口，再裹以胶布。由于瓶内空气少，温度稳定，这种方法保持效果也较好，且简便易行。"},{"text":"3、食品袋储藏法"},{"text":"先用洁净无异味白纸包好茶叶，再包上一张牛皮纸，然后装入一只无空隙的塑料食品袋内，轻轻挤压，将袋内空气挤出，随即用细软绳子扎紧袋口取一只塑料食品袋，反套在第一只袋外面，同样轻轻挤压，将袋内空气挤压在用绳子扎紧口袋，最后把它放进干燥无味的密闭的铁桶内。"},{"text":"4、低温贮存法"},{"text":"江华毛尖\n        装入密度高、高压、厚实、强度好、无异味的食品包装袋，将江华毛尖贮存的环境保持在5摄氏度以下，也就是使用冷藏库或冷冻库保存江华毛尖。"}]},{"name":"江华毛尖冲泡方法","content":[{"text":"1、赏茶：外形条索紧细、圆、光、直，白毫显露，有峰苗，色泽隐绿，油润光滑。"},{"src":"\\lvcha\\jianghuamaojian\\img_201911221001360l8l05.jpg"},{"text":"2、烫杯：用壶里的热水采用回旋斟水法浸润茶杯，提高茶杯的温度。"},{"text":"3、加水：水注到杯身的十分之七满，注水时注意水的温度要达到90℃(这样才能在投茶时使水温在85℃)。水用山泉水为最佳(目前市面的桶装矿泉水也可)，其次为江河水或溪水，再者是纯净水，最忌自来水，会糟蹋好茶的。"},{"text":"4、投茶：特级\n        江华毛尖\n        采用上投法。用茶匙把茶荷中的\n        信阳毛尖\n        均匀拨到玻璃杯中去。"},{"text":"5、静置：等待茶叶吸水下沉慢慢展开(浸泡时间为3～5分钟)。"},{"text":"6、品茶：江华毛尖内质香气高鲜，有熟板栗香，汤色鲜绿，滋味鲜醇，叶底嫩绿匀整。"}]},{"name":"江华毛尖功效作用","content":[{"text":"1、美容护肤，抗衰老。"},{"text":"江华毛尖\n        所含的抗氧化剂有助于抵抗老化。因为人体新陈代谢的过程，如果过氧化，会产生大量自由基，容易老化，也会使细胞受伤。SOD(超氧化物歧化)是自由基清除剂，能有效清除过剩自由基，阻止自由基对人体的损伤。江华毛尖中的儿茶素能显著提高SOD的活性，清除自由基。江华毛尖所含有的茶多酚，能够起到清除面部多余的油脂，收敛毛孔的作用。"},{"src":"\\lvcha\\jianghuamaojian\\img_201911221001369a2soc.jpg"},{"text":"2、塑身减脂。"},{"text":"江华毛尖含有茶碱及咖啡因，可以经由许多作用活化蛋白质激酶及三酸甘油酯解脂酶，减少脂肪细胞堆积，因此达到减肥功效。也就是茶叶中的咖啡碱能提高胃液的分泌量，可以帮助消化，增强分解脂肪的能力。"},{"text":"3、防辐射。"},{"text":"茶多酚及其氧化产物具有吸收放射性物质锶90和钴60毒害的能力。现在的白领阶层经常使用电脑，免不了会受到辐射的侵害。"},{"text":"4、提神醒脑。"},{"text":"江华毛尖茶叶中的咖啡碱能促使人体中枢神经兴奋，增强大脑皮层的兴奋过程，起到提神益思、清心的效果。所以建议大家在白天疲劳的时候也可以喝\n        绿茶\n        提神。"},{"text":"5、利尿解乏。"},{"text":"江华毛尖茶叶中的咖啡碱可刺激肾脏，促使尿液迅速排出体外，提高肾脏的滤出率，减少有害物质在肾脏中滞留时间。咖啡碱还可排除尿液中的过量乳酸，有助于使人体尽快消除疲劳。"},{"text":"6、防龋齿、清口臭。"},{"text":"江华毛尖含有氟，其中儿茶素可以抑制生龋菌作用，减少牙菌斑及牙周炎的发生。茶所含的单宁酸，具有杀菌作用，能阻止食物渣屑繁殖细菌，故可以有效防止口臭。"},{"text":"7、抵抗病毒菌。"},{"text":"茶多酚有较强的收敛作用，对病原菌、病毒有明显的抑制和杀灭作用，对消炎止泻有明显效果。对急性和慢性痢疾也有治愈作用。"}]}]}